.nav-wrapper {
  width: 100%;
  height: 96px;
  box-sizing: border-box;
  display: flex;
  overflow-x: hidden;
  justify-content: flex-end;
  position: absolute;
  z-index: 2; }

.navigation-full {
  width: 40vw;
  display: flex;
  justify-content: center;
  align-items: center; }
  .navigation-full ul {
    display: flex;
    justify-content: space-evenly;
    width: 100%;
    align-items: center;
    list-style: none; }
    .navigation-full ul li a {
      text-decoration: none;
      color: white; }
      .navigation-full ul li a:hover,
      .navigation-full ul li a :active {
        color: #efecc8; }

.navigation {
  display: flex;
  box-sizing: border-box;
  z-index: 2;
  justify-content: flex-start;
  align-items: flex-end;
  flex-direction: column;
  position: fixed;
  color: white; }
  .navigation__icon {
    width: 50px;
    height: 50px;
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 1000;
    color: white;
    padding: 1rem;
    transition: background-color 0.3s ease;
    background-color: transparent; }
    .navigation__icon:hover {
      background-color: rgba(9, 3, 23, 0.98);
      transition: background-color 0.2s ease; }
  .navigation__list {
    list-style: none;
    margin: 0;
    transition: all ease-in-out 500ms;
    transform: translateY(-500px);
    padding: 0;
    opacity: 0;
    background: rgba(9, 3, 23, 0.98); }
    .navigation__list--item {
      display: flex;
      justify-content: center;
      align-items: center;
      padding: 1rem;
      position: relative;
      opacity: 0;
      flex-direction: row;
      transition: all ease 200ms;
      margin: 1rem 0;
      width: 50px;
      max-height: 20vh; }
      .navigation__list--item p {
        position: absolute;
        opacity: 0;
        font-size: 1rem;
        margin: 0;
        padding: 0;
        transform: translateX(-50px);
        background-color: rgba(9, 3, 23, 0.98);
        transition: all ease 300ms;
        width: 60px;
        z-index: 100;
        justify-self: center;
        text-align: center;
        padding: 1rem; }
      .navigation__list--item:hover {
        cursor: pointer; }
        .navigation__list--item:hover p {
          transform: translateX(-5rem);
          opacity: 1;
          transition: all ease-in-out 250ms;
          width: 60px;
          cursor: default; }
          @media (max-width: 767px) {
            .navigation__list--item:hover p {
              display: none; } }
  .navigation .home-icon,
  .navigation .profile-icon,
  .navigation .work-icon,
  .navigation .mail-icon {
    height: 30px;
    width: 30px;
    color: white;
    transition: opacity ease-in-out 300ms; }
    .navigation .home-icon:hover,
    .navigation .profile-icon:hover,
    .navigation .work-icon:hover,
    .navigation .mail-icon:hover {
      opacity: 0.8;
      cursor: pointer; }
  .navigation .active {
    background-color: rgba(9, 3, 23, 0.98); }
  .navigation .open {
    transform: translateY(0px);
    opacity: 1; }
    .navigation .open .navigation__list--item {
      opacity: 1;
      transition: opacity 350ms ease 350ms; }
