.menu-button {
  right: 20px;
  top: 25px;
  cursor: pointer;
  padding: 0;
  width: 40px;
  height: 40px;
  z-index: 1000;
  border: none;
  background: none;
  font-size: 0;
  line-height: 0;
  -webkit-tap-highlight-color: transparent; }

.menu-button:focus, nav ul li a:focus {
  outline: none; }

.menu-button:focus::-moz-focus-inner, nav ul li a:focus::-moz-focus-inner {
  border: none; }

.menu-button:focus.show-outline {
  outline: 3px solid #fff;
  outline-color: var(--outline-color);
  outline-offset: -3px; }

.menu-button span, .menu-button span::before, .menu-button span::after {
  display: inline-block;
  width: 26px;
  height: 4px;
  border-radius: 2px;
  background: #fff; }

.menu-button span.hamburger-transition {
  background: rgba(0, 0, 0, 0); }

.menu-button span {
  position: relative; }

.menu-button span::before {
  content: "";
  background: #fff;
  position: absolute;
  left: 0;
  bottom: 8px;
  -webkit-transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transition: bottom 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }

.menu-button span.hamburger-transition::before {
  bottom: 0;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  -webkit-transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transition: bottom 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); }

.menu-button span::after {
  content: "";
  background: #fff;
  position: absolute;
  left: 0;
  top: 8px;
  -webkit-transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transition: top 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms cubic-bezier(0.23, 1, 0.32, 1); }

.menu-button span.hamburger-transition::after {
  top: 0;
  -webkit-transform: rotate(45deg);
  transform: rotate(45deg);
  -webkit-transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), -webkit-transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1);
  transition: top 300ms cubic-bezier(0.23, 1, 0.32, 1), transform 300ms 300ms cubic-bezier(0.23, 1, 0.32, 1); }
