body {
  margin: 0; }

.landing-page {
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 100vh;
  z-index: 1;
  padding: 100px 20px 0;
  background-color: transparent;
  box-sizing: border-box;
  display: flex;
  align-items: center; }
  .landing-page .page-wrapper {
    margin-left: 5rem; }
    @media (max-width: 767px) {
      .landing-page .page-wrapper {
        margin: 0 auto;
        text-align: center; } }
    @media (min-width: 1350px) {
      .landing-page .page-wrapper {
        margin: 0 auto;
        align-items: flex-end; } }
  .landing-page__title {
    color: #fff;
    font-size: 2.5em;
    margin: 0; }
    @media (max-width: 768px) {
      .landing-page__title {
        font-size: 1.6em; } }
    @media (min-width: 1350px) {
      .landing-page__title {
        text-align: center; } }
  .landing-page__subtitle {
    color: white;
    font-size: 23px;
    margin: 1rem 1px;
    opacity: 0.7;
    width: 90vw;
    max-width: 500px; }
    @media (max-width: 767px) {
      .landing-page__subtitle {
        margin: 0.5rem auto;
        font-size: 18px; } }
    @media (min-width: 1000px) {
      .landing-page__subtitle {
        width: 100%; } }
    @media (min-width: 1350px) {
      .landing-page__subtitle {
        text-align: center;
        margin: 1rem auto; } }
  @media (max-width: 767px) {
    .landing-page__button-container {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center; } }
  @media (min-width: 1350px) {
    .landing-page__button-container {
      display: flex;
      justify-content: center;
      align-items: center;
      text-align: center; } }
  .landing-page__button-container .button-primary {
    margin: 1rem 1.75rem 0 0;
    width: 200px; }
    @media (max-width: 767px) {
      .landing-page__button-container .button-primary {
        width: 80vw;
        margin: 0.5rem auto; } }
    @media (min-width: 1350px) {
      .landing-page__button-container .button-primary {
        margin: 1rem 1.75rem; } }
