.work-page {
  position: absolute;
  top: 0;
  width: 100%;
  min-height: 100vh;
  overflow-x: hidden;
  z-index: 1;
  padding: 100px 20px 0;
  background-color: transparent;
  box-sizing: border-box; }
  .work-page .img-container {
    display: flex;
    margin-top: 6rem;
    flex-direction: column; }
    .work-page .img-container hr {
      border: 0;
      width: 100%;
      margin: 2rem 0;
      border-bottom: 1px dashed rgba(255, 255, 255, 0.25);
      background: rgba(255, 255, 255, 0.25); }
  .work-page__grid {
    display: flex;
    align-items: center; }
    @media (max-width: 767px) {
      .work-page__grid {
        flex-direction: column; } }
    .work-page__grid--container {
      text-align: center;
      margin: 0 auto; }
      .work-page__grid--container h3 {
        display: inline-block;
        margin: 2rem 0 0;
        padding: 0; }
      .work-page__grid--container p {
        padding: 0;
        margin: 0 auto; }
      .work-page__grid--container ul {
        margin: 0;
        padding: 0;
        display: inline-block; }
        .work-page__grid--container ul li {
          margin: .5rem;
          display: inline-block;
          color: rgba(255, 255, 255, 0.84); }
      .work-page__grid--container .button-primary {
        margin: 2em 1em;
        width: 8rem; }
    .work-page__grid--img {
      object-fit: cover;
      height: 300px;
      width: 400px;
      border: 1px solid black;
      transition: transform ease 400ms;
      border-radius: 5px; }
      .work-page__grid--img:hover {
        transform: scale(1.015) skew(0.75deg); }
  .work-page__button-container {
    display: flex;
    justify-content: center; }
    .work-page__button-container .button-primary {
      margin: 2em 1em 4em;
      width: 10rem; }
      @media (max-width: 767px) {
        .work-page__button-container .button-primary {
          width: 70vw; } }
    @media (max-width: 767px) {
      .work-page__button-container {
        width: 100%;
        display: flex;
        justify-content: center; } }
